}

.container {
  max-width: 120rem;
  margin: 0 auto;
}

.heading {
  font-size: 3rem;
  font-family: "Montseratt", sans-serif;
  text-align: center;
  font-weight: 500;
}

.underline {
  width: 5rem;
  height: 0.3rem;
